$namespace: ".subscribe";

#{$namespace} {
    border-top: $bsu solid color(section, highlighted-bg);

    .lead {
        margin: 0 0 $bsu * 1.5;

        /**
         * Media query adjustments.
         */
        @include media-query(3) {
            margin: 0 0 $bsu * 2;
            max-width: 50%;
        }
    }
}