$namespace: ".site-header";

#{$namespace} {

    &--compact {
        @extend .cf;

        background-color: color(site-header, compact-bg);
        padding: $bsu * .75 $bsu * .75;

        .logo {
            float: left;
            height: 90px;
            position: relative;
            top: -6px;
            width: 90px;

            /**
             * Media query adjustments
             */
            @include media-query(2) {
                height: 120px;
                width: 120px;
            }
        }

        /**
         * Media query adjustments
         */
        @include media-query(2) {
            padding: $bsu * .75 $bsu * 2.25;
        }
    }

    &--expanded {
        background-color: color(site-header, expanded-bg);
        padding: 0 $bsu;
        margin-bottom: $bsu * 6;

        .learn-more {
            padding-top: $bsu * 3;
        }

        .section-summaries {
            bottom: -$bsu * 4;
            margin-top: -$bsu;
        }

        /**
         * Media query adjustments
         */
        @include media-query(3) {
            padding: $bsu * 6 $bsu * 1.25 0;
        }

        /**
         * Media query adjustments.
         */
        @include media-query(4) {
            padding: $bsu * 6 $bsu * 2.5 0;
        }
    }
}