 * Simple, boxed off content, as per: csswizardry.com/2011/10/the-island-object
 * E.g.:
 *
   <div class=island>
       I am boxed off.
   </div>
 */
.island,
.islet {
  display: block;
}

.island {
  padding: 26px;
}

.island > :last-child,
.islet > :last-child {
