$namespace: ".section";

#{$namespace} {
    padding: $bsu * 2 0;

    &:last-of-type {
        padding-bottom: $bsu * 3;
    }

    &__header {
        margin: $hsu 0 $bsu;

        /**
         * Media query adjustments
         */
        @include media-query(3) {
            margin: $hsu 0 $bsu * 1.5;
        }
    }
    &__title {
        @extend .extra-large-text, .regular-serif;

        margin-bottom: $hsu;

        a {
            @extend %inverted-anchor;
        }
    }

    &__body {

        img:last-of-type {
            float: left;
            margin: 0;
        }
    }

    &__column {
        position: relative;

        /**
         * Media query adjustments
         */
        @include media-query(4) {

            &--right-divider {

                &:before {
                    border: 2px solid color(section, divider);
                    display: block;
                    content: "";
                    height: 100%;
                    position: absolute;
                    right: - $bsu * 1.5 - 2px;
                    top: 0;
                }
            }

            &--left-divider {

                &:before {
                    border: 2px solid color(section, divider);
                    display: block;
                    content: "";
                    height: 100%;
                    left: - $bsu * 1.5 - 2px;
                    position: absolute;
                    top: 0;
                }
            }
        }
    }

    &__footer {
        clear: left;
        margin: 0 0 $hsu;
        padding-top: $bsu * 1.5;

        &--centered {
            text-align: center;
        }
    }

    &--highlighted {
        background-color: color(section, highlighted-bg);
    }

    &--secondary {
        background-color: color(section, secondary-bg);
        color: color(section, secondary-text);
    }

    &--two-col {

        /**
         * Media query adjustments.
         */
        @include media-query(3) {

            #{$namespace} {

                &__header {
                    float: left;
                    width: 33.333%;
                    padding: 0 $bsu * 1.5;
                }
                &__body {
                    float: left;
                    width: 66.666%;
                    padding: 0 $bsu * 1.5;
                }
            }
        }
    }

    &--no-padding {
        padding: 0;
    }
    
    &--padded {

        /**
         * Media query adjustments
         */
        @include media-query(3) {
            padding: $bsu * 4 0;
        }
    }
}