$namespace: ".logo";

#{$namespace} {
    background: color(logo, bg) url(../../../images/site/logo--red.svg) center no-repeat;
    background-size: 70% auto;
    border-radius: 150px;
    display: block;
    height: 118px;
    padding: $bsu;
    text-indent: -9999px;
    width: 118px;

    a {
        display: block;
        height: 118px;
        width: 118px;
    }

    /**
     * Media query adjustments.
     *
     * Logo progressively scales up at higher resolutions.
     */
    @include media-query(3) {
        height: 177px;
        width: 177px;

        a {
            display: block;
            height: 177px;
            width: 177px;
        }
    }

    /**
     * Media query adjustments.
     */
    @include media-query(4) {
        height: 236px;
        width: 236px;

        a {
            display: block;
            height: 236px;
            width: 236px;
        }
    }

    &--inverted {
        background-color: color(logo, inverted-bg);
        background-image: url(../../../images/site/logo.svg);
    }
}