$namespace: ".page-header";

#{$namespace} {
    clear: left;
    padding: $hsu 0;

    &__title {
        @extend .regular-serif, .extra-large-text;

        margin-bottom: 0;
    }

    /**
     * Media query adjustments
     */
    @include media-query(2) {
        clear: none;
        float: left;
        padding: $bsu $bsu * 8 $bsu $bsu * 3;
    }
}