$namespace: ".page-header";

#{$namespace} {

    /**
     * Media query adjustments
     */
    @include media-query(2) {
        left: $bsu * 6;
        position: absolute;
        top: $bsu * .75;
    }

    /**
     * Media query adjustments
     */
    @include media-query(3) {
        top: $bsu * 1.5;
    }
}