/*------------------------------------*\
    $COLORS
\*------------------------------------*/
/**
 * Set project brand variables first. These can then be included in the color map.
 * Details of how to use the color map can be found here: https://medium.com/p/clean-colour-management-with-sass-4d5e9bb4f1b2
 */
$brandRed: #CC4E46;
$brandBlack: #000000;
$brandDarkTeal: #1A6A68;
$brandTeal: #59ABA9;
$brandLightTeal: #C5ECEB;
$brandHighlight: #F5F5F5;
$brandDivider: #E9E9E9;
$brandWhite: #FFFFFF;

$baseHeading: #000000;
$baseBody: rgba(#000000, 0.45);
$baseInvertedHeading: #FFFFFF;
$baseInvertedBody: rgba(#FFFFFF, 0.6);

$colors: (

    base: (
        body:           $baseBody,
        heading:        $baseHeading,

        anchor:         $brandRed,
        anchor-hover:   $brandBlack,
        anchor-visited: $brandRed,

        table-bg: $brandWhite,
        table-border:   #CCCCCC,
        table-head: $brandHighlight,
        table-stripe:   $brandHighlight,

        pull-quote: $baseHeading,

        blockquote: $brandTeal
    ),

    state: (
        success-text:   #468847,
        success-bg:     #DFF0D8,
        success-border: darken(adjust-hue(#DFF0D8, -10), 5%),

        info-text:      #3A87AD,
        info-bg:        #D9EDF7,
        info-border:    darken(adjust-hue(#D9EDF7, -10), 7%),

        warning-text:   #C09853,
        warning-bg:     #FCF8E3,
        warning-border: darken(adjust-hue(#FCF8E3, -10), 5%),

        danger-text:    #B94A48,
        danger-bg:      #F2DEDE,
        danger-border:  darken(adjust-hue(#F2DEDE, -10), 5%)
    ),

    input: (
        bg: $brandWhite,
        text: $brandBlack,
        border: $brandDivider,
        placeholder: $brandBlack
    ),

    btn: (
        default-bg: $brandBlack,
        default-text: $brandBlack,

        cta-bg: $brandRed,
        cta-text: $brandRed,

        secondary-bg: $brandTeal,
        secondary-text: $brandTeal,

        inverted-bg: $brandWhite,
        inverted-text: $brandWhite
    ),

    main: (
        body-border: $brandWhite,
        content-bg: $brandHighlight
    ),

    logo: (
        bg: $brandWhite,
        inverted-bg: $brandRed
    ),

    nav: (
        primary-bg: $brandWhite,
        primary-border: $brandRed,
        primary-outer-border: $brandWhite,
        primary-title: $brandBlack,
        primary-title-hover: $brandRed,
        primary-desc: $brandBlack,
        primary-divider: $brandDivider,
        secondary-link: $brandBlack,
        secondary-link-hover: $brandRed,
        social-link: $brandTeal,
        social-link-hover: $brandDarkTeal,
        handle: $brandRed,
        handle-inverted: $brandWhite,
        breadcrumb-text: $baseBody,
        breadcrumb-active: $baseHeading,
        sidebar-divider: #E0E6ED,
        sidebar-text: $brandBlack,
        sidebar-text-hover: $brandRed,
        sidebar-parent-bg: $brandHighlight,
        sidebar-parent-border: $brandRed,
        sidebar-child-bg: $brandWhite,
        sidebar-child-border: $brandWhite,
    ),

    resource: (
        title: $baseHeading,
        meta: $baseHeading,
        text: $baseBody,
        title-hover: $baseInvertedHeading,
        text-hover: $baseInvertedBody,
        anchor-hover: $brandWhite,
        bg: $brandWhite,
        bg-alt: $brandBlack,
        bg-hover: $brandRed,
        bg-hover-alt: $brandBlack,
    ),

    podcast-banner: (
        title: $baseHeading,
        title-hover: $baseInvertedHeading,
        bg: $brandRed,
        shadow-hover: $brandRed,
    ),

    section-heading: (
        title: $brandRed,
        icon: $brandWhite,
        icon-bg: $brandRed,
    ),

    learn-more: (
        text: $brandWhite
    ),

    site-header: (
        compact-bg: $brandWhite,
        expanded-bg: $brandRed,
    ),

    section: (
        highlighted-bg: $brandWhite,
        secondary-bg: $brandLightTeal,
        secondary-text: $brandDarkTeal,
        divider: $brandDivider
    ),

    section-summaries: (
        bg: $brandWhite
    ),

    subscription-form: (
        bg: $brandTeal,
        text: $brandWhite,
        border: $brandTeal,
        placeholder: $brandLightTeal
    ),

    welcome: (
        title: $brandWhite,
        text: $brandWhite,
        logo: $brandRed,
        logo-bg: $brandWhite
    ),

    entry: (
        bg: $brandWhite,
        text: $brandBlack,
        divider: $brandHighlight
    ),

    post: (
        divider: $brandHighlight
    ),

    meta-block: (
        bg: $brandRed,
        title: $brandWhite,
        text: $brandWhite,
        icon-bg: $brandWhite
    ),

    intro: (
        text: $brandBlack
    ),

    filter-bar: (
        bg: $brandRed,
        text: $brandWhite
    ),

    site-footer: (
        bg: $brandWhite
    )
);