$namespace: ".entry";

#{$namespace} {
    border-top: 5px solid color(entry, divider);
    background-color: color(entry, bg);
    position: relative;

    .nav--sidebar-menu {

        /**
         * Media query adjustments
         */
        @include media-query(3) {
            left: 0;
            position: absolute;
            right: -$bsu * 2;
            top: $bsu * 3;
        }
    }

    .grid__column--one-sixth {

        /**
         * Media query adjustments
         */
        @include media-query(3) {
            left: 0;
            position: absolute;
        }
    }
    .grid__column--five-sixths {

        /**
         * Media query adjustments
         */
        @include media-query(3) {
            margin-left: 16.666%;
        }
    }

    &__sidebar {
        padding-bottom: $bsu * 4;
    }

    &__body {
        @extend .medium-text;

        padding-top: $bsu;

        .lead {
            @include type-scale(18);

            /**
             * Media query adjustments
             */
            @include media-query(3) {
                @include type-scale(21, $bp2-font-base);
            }
        }

        .btn {
            margin: 0 $bsu $bsu 0;

            + .btn {
                margin-bottom: 0;
            }
        }

        > h2 {
            @extend .extra-large-text;
            @include font-stack(bold, sans-serif);
        }
        > h3 {
            @extend .large-text;
            @include font-stack(bold, sans-serif);
        }
        > h4 {
            @extend .large-text;
        }
        > h5 {
            @extend .medium-text;
        }
        > h6 {
            @extend .medium-text;
        }

        table {
            margin: $bsu * 2 0;
        }

        .meta-block {
            margin: $bsu * 2 0;
        }

        /**
         * Media query adjustments
         */
        @include media-query(3) {
            border-left: 5px solid color(entry, divider);
            min-height: 1000px;
            padding: $bsu * 3 $bsu * 4;
        }
    }
}