$namespace: ".intro";

#{$namespace} {
    @extend .cf;

    margin: 0 auto;
    padding: 0 $bsu;
    width: 100%;

    &__image {
        display: block;
        margin: 0 auto $hsu;
        max-width: 300px;
        width: 66.666%;
    }

    &__body {
        height: 100%;
        color: color(intro, text);
    }

    /**
     * Media query adjustments
     */
    @include media-query(2) {
        align-content: center;
        display: flex;
        justify-content: center;

        &__image {
            margin: 0;
            padding: 0 $bsu * 1.5 0 0;
            width: 45%;
        }
    }

    /**
     * Media query adjustments
     */
    @include media-query(3) {
        width: 75%;
    }
}