$namespace: ".filter-bar";

#{$namespace} {
    background-color: color(filter-bar, bg);
    color: color(filter-bar, text);
    padding: $hsu $bsu 0;

    &__items {
        @extend .cf;

        list-style: none;
        margin: 0;
        padding: 0;
    }

    &__item {
        float: left;
        margin-bottom: $hsu;
        margin-right: 5%;
        width: 45%;

        &--select {
            position: relative;

            &:after {
                display: block;
                font-family: 'icomoon' !important;
                content: "\e90a";
                position: absolute;
                right: 0;
                top: 0;
                z-index: 1;
            }
        }
    }

    &__select {
        @extend .bold;

        appearance: none;
        background-color: transparent;
        background-image: none;
        border: none;
        outline: none;
        padding-right: $hsu;
        position: relative;
        z-index: 2;
    }

    /**
     * Media query adjustments
     */
    @include media-query(3) {
        padding: $hsu $bsu * 2.25;

        &__item {
            display: inline-block;
            margin-bottom: 0;
            margin-right: $bsu * 1.5;
            width: auto;
        }
    }
}