@font-face {
    font-family: 'icomoon';
    src:  url('../../../fonts/icomoon.eot?tsx43x');
    src:  url('../../../fonts/icomoon.eot?tsx43x#iefix') format('embedded-opentype'),
    url('../../../fonts/icomoon.woff2?tsx43x') format('woff2'),
    url('../../../fonts/icomoon.ttf?tsx43x') format('truetype'),
    url('../../../fonts/icomoon.woff?tsx43x') format('woff'),
    url('../../../fonts/icomoon.svg?tsx43x#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon--"], [class*=" icon--"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

