$namespace: ".meta-block";

#{$namespace} {
    @extend .default-text, .cf;

    background-color: color(meta-block, bg);
    border-radius: global(border-radius);
    box-shadow: global(black-outer-glow);
    color: color(meta-block, text);
    padding: $bsu * 1.5 $bsu;

    &__aside {

        p {
            margin-bottom: 0;
        }

        /**
         * Media query adjustments
         */
        @include media-query(3) {
            float: left;
            width: 33.333%;
        }
    }

    &__body {
        clear: left;

        p:last-of-type {
            margin-bottom:0;
        }

        /**
         * Media query adjustments
         */
        @include media-query(3) {
            clear: none;
            float: left;
            width: 66.666%;
        }
    }

    &__title {
        @include type-scale($medium-text, $font-base);

        color: color(meta-block, title);
        margin-bottom: $hsu * .5;

        /**
         * Media query adjustments.
         */
        @include media-query(2) {
            @include type-scale($bp2-medium-text, $bp2-font-base);
        }
    }

    &__items {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    &__item {
        list-style: none;
        margin-bottom: $bsu;

        /**
         * Media query adjustments
         */
        @include media-query(1) {
            float: left;
            width: 50%;
        }

        /**
         * Media query adjustments
         */
        @include media-query(2) {
            width: 33.333%;
        }

        /**
         * Media query adjustments
         */
        @include media-query(3) {
            float: none;
            width: 100%;
        }
    }

    .icon {
        @extend .small-text;

        background-color: color(meta-block, text);
        border-radius: global(half-border-radius);
        margin: 0 $hsu * .5;
        padding: 6px;

        &--exit-arrow {
            padding-right: 4px;
        }
    }
}