$namespace: ".post";

#{$namespace} {
    background-color: $brandWhite;
    padding: 0 $bsu * .75;

    &__header {
        margin-bottom: $bsu * 2;

        /**
         * Media query adjustments
         */
        @include media-query(3) {
            padding-left: 11.111%;
            width: 88.888%;
        }
    }

    &__title {
        @extend .regular-serif;
    }

    &__meta {
        @extend .small-text;

        text-transform: uppercase;
    }

    &__body {
        @extend .medium-text;

        padding-bottom: $bsu * 2;

        .lead {
            @include type-scale(18);

            /**
             * Media query adjustments
             */
            @include media-query(3) {
                @include type-scale(21, $bp2-font-base);
            }
        }

        figure {
            @extend .cf;

            margin: 0 0 $bsu * 1.5;

            img {
                float: left;
                margin-bottom: 0;
                width: 100%;
            }
        }

        figcaption {
            @extend .small-text;

            clear: left;
            display: block;
            padding-top: $hsu * .5;
            text-align: center;
        }

        .figure--full-width {

            /**
             * Media query adjustments
             */
            @include media-query(3) {
                left: -22.222%;
                position: relative;
                width: 122.222%;
            }
        }

        .figure--left {
            float: left;
            margin-right: $bsu * 1.5;
            width: 50%;
        }

        > h2 {
            @extend .extra-large-text;
            @include font-stack(bold, sans-serif);
        }
        > h3 {
            @extend .large-text;
            @include font-stack(bold, sans-serif);
        }
        > h4 {
            @extend .large-text;
        }
        > h5 {
            @extend .medium-text;
        }
        > h6 {
            @extend .medium-text;
        }

        .blockquote--full-width {
            @extend .extra-large-text;

            text-align: center;

            /**
             * Media query adjustments
             */
            @include media-query(3) {
                left: -22.222%;
                position: relative;
                width: 122.222%;
            }
        }

        /**
         * Media query adjustments
         */
        @include media-query(3) {
            padding-left: 22.222%;
            width: 88.888%;
        }
    }

    &__comments {
        border-top: 5px solid color(post, divider);

        padding: $bsu * 2 11.111% $bsu * 2 22.222%;
    }

    /**
     * Media query adjustments
     */
    @include media-query(3) {
        padding: 0 $bsu * 2.25;
    }
}