@if $use-island == true{

/*------------------------------------*\
    $ISLAND
\*------------------------------------*/
/**
 * Simple, boxed off content, as per: csswizardry.com/2011/10/the-island-object
 * E.g.:
 *
   <div class=island>
       I am boxed off.
   </div>
 */
.island,
.islet {
    @extend .cf;
    
    display: block;
}
.island{
    padding: $base-spacing-unit;
}
    .island > :last-child,
    .islet > :last-child{
        margin-bottom: 0;
    }


/**
 * Just like `.island`, only smaller.
 */
.islet{
    padding: $half-spacing-unit;
}

}//endif