$namespace: ".subscription-form";

#{$namespace} {
    @extend .cf;

    &__email[type=text] {
        background-color: color(subscription-form, bg);
        border: 1px solid color(subscription-form, border);
        color: color(subscription-form, text);
        float: left;
        margin-right: $hsu;
        width: 220px;

        &::placeholder {
            color: color(subscription-form, placeholder);
        }

        /**
         * Media query adjustments
         */
        @include media-query(2) {
            max-width: 360px;
            width: 100%;
        }

        /**
         * Media query adjustments.
         */
        @include media-query(4) {
            max-width: 500px;
        }
    }
    &__submit {
        float: left;
    }
}