$namespace: ".welcome";

#{$namespace} {
    @extend .cf;

    color: color(welcome, text);
    padding: $bsu 0;
    position: relative;

    &__header {

        /**
         * Media query adjustments.
         *
         * Welcome header becomes absolutely positioned a breakpoint 4.
         */
        @include media-query(4) {
            left: 0;
            position: absolute;
            top: $bsu;
            width: 290px;
        }
    }
    &__logo {
        height: 118px;
        margin-bottom: $bsu * 2;
        width: 118px;

        /**
         * Media query adjustments.
         *
         * Logo progressively scales up at higher resolutions.
         */
        @include media-query(3) {
            height: 177px;
            width: 177px;
        }

        /**
         * Media query adjustments.
         */
        @include media-query(4) {
            height: 236px;
            width: 236px;
        }
    }

    &__body {

        /**
         * Media query adjustments.
         *
         * We add some left padding to account for the title overlap.
         */
        @include media-query(3) {
            padding-left: $bsu * 3;
        }
        
        /**
         * Media query adjustments.
         *
         * Logo becomes absolutely positioned and left aligned at
         * breakpoint 4 so we apply padding to account for this here.
         */
        @include media-query(4) {
            padding-left: 290px + $bsu * 2;
        }
    }
    &__title {
        @extend .regular-italic-serif, .giga;

        color: color(welcome, title);
        margin-bottom: $bsu;

        /**
         * Media query adjustments.
         *
         * We apply and indent effect to the welcome title at
         * breakpoint 3.
         */
        @include media-query(3) {

            span {
                display: block;
                left: -$bsu * 2;
                position: relative;
            }
        }
    }
    &__lead {

        /**
         * Media query adjustments.
         *
         * We restrict the width of the lead paragraph.
         */
        @include media-query(3) {
            padding-left: 20px;
            width: 65%;
        }

        /**
         * Media query adjustments
         */
        @include media-query(4) {
            width: 45%;
        }
    }

    /**
     * Media query adjustments.
     */
    @include media-query(3) {
        padding: $bsu * 2 0;
    }
}