$namespace: ".resource";
$placeholder: "%resource";

/**
 * There are some common styles that we are reusing through Sass placeholder selectors.
 *
 * 1. Apply a container to the resource.
 * 2. Invert the resource text on hover.
 * 3. Changed resource background to red on hover.
 * 4. Changed resource background to black on hover.
 */
#{$placeholder} {

    &-container { /* [1] */
        background-color: color(resource, bg);
        border-radius: 12px;
        box-shadow: global(black-outer-glow);
    }

    &-text-hover { /* [2] */

        &:hover {

            #{$namespace}__title {
                color: color(resource, title-hover);
            }
            #{$namespace}__body {
                color: color(resource, text-hover);
            }
            #{$namespace}__footer {
                color: color(resource, anchor-hover);
            }
        }
    }

    &-hover { /* [3] */

        &:hover {
            @extend #{$placeholder}-text-hover;

            background-color: color(resource, bg-hover);
            box-shadow: global(red-outer-glow-hover);
        }
    }

    &-hover-alt { /* [4] */

        &:hover {
            @extend #{$placeholder}-text-hover;

            background-color: color(resource, bg-hover-alt);
            box-shadow: global(black-outer-glow-hover);
        }
    }
}

/**
 * Component styles.
 */
#{$namespace} {
    margin-bottom: $bsu * 2;

    &:last-of-type {
        margin-bottom: 0;
    }

    /**
     * Global styles.
     */
    &__header {
        padding: $bsu * 1.25 $bsu 0;
    }
    &__title {
        @extend .medium-text;

        color: color(resource, title);
        line-height: 1.4;
        margin-bottom: $hsu * .5 !important;

        a {
            @extend %underline-anchor;
        }

        + #{$namespace}__meta {
            margin-bottom: $hsu * .5;
        }
    }
    &__meta {
        color: color(resource, meta);
        position: relative;
        top: -.5em;

        a {
            @include font-stack(bold, sans-serif);
        }
    }
    &__body {
        @extend .cf;

        color: color(resource, text);
        padding: 0 $bsu;

        p:last-of-type {
            margin-bottom: 0;
        }

        img {
            float: left;
            margin: 0;
        }
    }
    &__image {

        img {
            float: left;
            border-radius: 12px;
            margin: 0;
        }
    }
    &__link {
        display: block;
        text-decoration: none;
    }
    &__footer {
        @extend .small-text;
        @include font-stack(bold, sans-serif);

        padding: $bsu $bsu $bsu * .75;
        text-transform: uppercase;

        a {
            text-decoration: none;
        }
    }

    /**
     * Transitions.
     */
    transition: all .25s ease;

    &__title,
    &__body,
    &__footer {
        transition: color .25s ease;
    }

    /**
     * Resource type modifiers.
     *
     * 1. Basic: The basic resource comprises only of text.
     * 2. Two Column: The two column resource can have the same content as a basic resource
     *    along with an image e.g. a book cover beside it.
     *     2a: As well as appling spacing between the two columns we're also adding
     *         some bottom padding on the first column to avoid overlap with the
     *         absolutely positioned footer.
     *     2b: We're absolutely positioning the footer so that it's always positioned
     *         at the bottom of the element.
     * 3. Media: The media resource comprises of a title and an image.
     * 4. Video: The video resource should have a placeholder image for the video it links to.
     *     4a: Overlay a video play icon above the video placeholder.
     * 5. Podcast: The podcast resource comprises of summary information for the given episode.
     * 6. Post: The post resource comrpises of summary information for the given post.
     */
    &--basic { /* [1] */
        @extend #{$placeholder}-container, #{$placeholder}-hover;
    }

    &--two-column {  /* [2] */
        @extend #{$placeholder}-container, #{$placeholder}-hover;

        #{$namespace} {

            &__link {
                @extend .cf;

                position: relative;
            }
            &__column {
                @extend .cf;

                float: left;
                width: 50%;

                &:first-of-type { /* [2a] */
                    padding: 0 $hsu $bsu * 2 0;
                }
                &:last-of-type { /* [2a] */
                    padding: $hsu $bsu * .75;
                }

                img {
                    float: left;
                    margin: 0;
                }
            }
            &__header,
            &__body,
            &__footer {
                padding-right: 0;
            }

            &__footer { /* [2b] */
                bottom: 0;
                left: 0;
                position: absolute;
            }
        }
    }

    &--media {  /* [3] */
        @extend #{$placeholder}-container, #{$placeholder}-hover-alt;

        #{$namespace} {

            &__body {
                padding: 0;
            }
        }

        img {
            border-radius: 0 0 12px 12px;
        }
    }

    &--video {  /* [4] */
        @extend #{$placeholder}-container, #{$placeholder}-hover-alt;

        #{$namespace} {

            &__body {
                padding: 0;
                position: relative;

                &:before { /* [4a] */
                    background: rgba(color(resource, bg-alt), 0.7) url(../../../images/site/video-icon.svg) center no-repeat;
                    background-size: 60% auto;
                    border-radius: 12px;
                    bottom: $bsu * .75;
                    display: block;
                    content: "";
                    height: 60px;
                    position: absolute;
                    right: $hsu;
                    transition: background-color .25s ease;
                    width: 80px;
                    z-index: 2;
                }
            }
        }

        &:hover {

            #{$namespace} {

                &__body {

                    &:before {
                        background-color: color(resource, bg-hover);
                    }
                }
            }
        }

        img {
            border-radius: 0 0 12px 12px;
        }
    }

    &--podcast {  /* [5] */

        #{$namespace} {

            &__header,
            &__body,
            &__footer {
                padding: 0;
            }
        }
    }

    &--post {  /* [6] */

        #{$namespace} {

            &__header,
            &__body,
            &__footer {
                padding: 0;
            }
            &__footer {
                padding-top: $bsu;
            }
            &__image {

                a {
                    @extend .cf;

                    display: block;
                }

                img {
                    margin-bottom: $bsu;
                    opacity: .6;
                    transition: opacity .25s ease, box-shadow .25s ease;
                    width: 100%;
                }
            }
        }

        &:hover {

            #{$namespace} {

                &__image {

                    img {
                        box-shadow: global(black-outer-glow);
                        opacity: 1;
                    }
                }
            }
        }
    }

    /**
     * Resource state modifiers.
     *
     * 1. Wide: Use in conjunction with the post type modifier to span two columns.
     * 2. Small: Reduce type scale for resource.
     */
    &--wide { /* [1] */
        @extend .cf;

        /**
         * Media query adjustments
         */
        @include media-query(2) {
            clear: left;

            #{$namespace} {

                &__image,
                &__content {
                    float: left;
                    width: 50%;
                }
                &__image {
                    padding-right: $hsu;
                }
                &__content {
                    padding: 0 $bsu 0 $hsu;
                }
            }
        }

        /**
         * Media query adjustments
         */
        @include media-query(4) {

            &__image {
                padding-right: $bsu * 1.5;
            }
            &__content {
                padding: 0 $bsu 0 $bsu * 1.5;
            }
        }
    }

    &--small { /* [2] */

        #{$namespace} {

            &__title {
                @extend .default-text;
            }
            &__meta,
            &__body {
                @extend .small-text;
            }
        }
    }
}