$namespace: ".site-footer";

/**
 * Site footer UI styles with child component overrides/adjustments.
 *
 * 1.
 */
#{$namespace} {
    background-color: color(site-footer, bg);
    padding: $bsu * 2 $bsu * .75 $bsu * .75;

    .nav--secondary { /* [1] */
        float: left;
        margin-bottom: $bsu * 1.5;
    }

    .nav--social { /* [2] */
        float: right;
        margin-bottom: $bsu * 1.5;
        position: relative;
        top: -5px;
    }

    &__copyright {
        @extend .small-text;

        clear: both;
        margin-bottom: 0;

        a {
            @extend %inverted-anchor;
        }
    }

    /**
     * Media query adjustments.
     *
     * We increase the site footer padding at breakpoint 3.
     */
    @include media-query(3) {
        padding: $bsu * 2;
    }
}