$namespace: ".btn";

/**
 * Component styles.
 */
#{$namespace} {
    @extend .small-text;

    background-color: color(btn, default-bg);
    border-radius: 12px;
    border: none;
    color: color(btn, inverted-text);
    cursor: pointer;
    display: inline-block;
    letter-spacing: 1px;
    line-height: 14px;
    padding: $bsu * .5 $bsu * .75;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    transition: all .25s ease;

    &:active,
    &:focus,
    &:visited {
        color: color(btn, inverted-text);
    }

    &:hover,
    &:visited:hover {
        background-color: color(btn, inverted-bg);
        box-shadow: 0 0 30px 0 rgba(color(btn, default-text), 0.2);
        color: color(btn, default-text);
    }

    /**
     * Media query adjustments.
     *
     * We scale up the buttons padding at breakpoint 2. The
     * type scale is increased via the .small-text helper.
     */
    @include media-query(2) {
        padding: $bsu * .7 $bsu * 1.125;
    }

    /**
     * Button type modifiers.
     *
     * 1. Inverted: A flipped version of the default button.
     * 2. Call-To-Action: A call to action version of the button.
     *     2a: The call to action button can be inverted to be used over a darker background
     *         by combining it with the inverted modifier.
     * 3. Secondary: The secondary button is typically used in the subscription section.
     *     3a: The secondary button can be inverted to be used over a darker background
     *         by combining it with the inverted modifier.
     * 4. Alt: Alternative hover state.
     */
    &--inverted { /* [1] */
        background-color: color(btn, inverted-bg);
        color: color(btn, default-text);

        &:hover,
        &:visited:hover {
            background-color: color(btn, default-bg);
            box-shadow: none;
            color: color(btn, inverted-text) !important;
        }
    }

    &--cta { /* [2] */
        background-color: color(btn, cta-bg);
        color: color(btn, inverted-text);

        &:hover,
        &:visited:hover {
            background-color: color(btn, inverted-bg);
            box-shadow: 0 0 30px 1px rgba(color(btn, cta-text), 0.2);
            color: color(btn, cta-text) !important;
        }

        &#{$namespace}--inverted { /* [2a] */
            background-color: color(btn, inverted-bg);
            color: color(btn, cta-text);

            &:hover,
            &:visited:hover {
                background-color: color(btn, default-bg);
                box-shadow: none;
                color: color(btn, inverted-text) !important;
            }
        }
    }

    &--secondary { /* [3] */
        background-color: color(btn, secondary-bg);
        color: color(btn, inverted-text);

        &:hover,
        &:visited:hover {
            background-color: color(btn, inverted-bg);
            box-shadow: 0 0 30px 1px rgba(color(btn, secondary-text), 0.3);
            color: color(btn, secondary-text) !important;
        }

        &#{$namespace}--inverted { /* [3a] */
            background-color: color(btn, inverted-bg);
            color: color(btn, secondary-text);

            &:hover,
            &:visited:hover {
                background-color: color(btn, secondary-bg);
                box-shadow: 0 0 30px 1px rgba(color(btn, secondary-text), 0.3);
                color: color(btn, inverted-text) !important;
            }
        }
    }

    &--alt { /* [4] */

        &:hover,
        &:visited:hover {
            background-color: color(btn, default-bg);
            box-shadow: none;
            color: color(btn, inverted-text) !important;
        }
    }

    &--alt-cta {

        &:hover,
        &:visited:hover {
            background-color: color(btn, cta-bg);
            box-shadow: none;
            color: color(btn, inverted-text) !important;
        }
    }
}