$namespace: ".section-summaries";

/**
 * Component styles.
 */
#{$namespace} {
    @extend .cf;

    list-style: none;
    margin: 0;
    padding: 0;
    position: relative;

    &__item {
        @extend .cf;

        background-color: color(section-summaries, bg);
        box-shadow: global(black-outer-glow);
        border-radius: 12px;
        margin-bottom: $bsu * 2;
        padding: $hsu $bsu $bsu;
        position: relative;
        text-align: center;
        z-index: 1;

        &:last-of-type {
            margin-bottom: 0;
        }

        /**
         * Media query adjustments.
         *
         * At breakpoint 1 we set the item to flex so that the image and
         * text are side by side.
         */
        @include media-query(1) {
            display: flex;
            padding: $bsu;
        }

        /**
         * Media query adjustments.
         *
         * At breakpoint 2 we revert back to the stacked view and add the
         * skewed corners on the first and last items.
         *
         * 1. We add an :after element to the item, skew it with a transform and
         *    position it to overlap the bottom of the item.
         * 2. Because we want to maintain a constant shadow around the item we've
         *    applied a shadow around the :after element as well. We add a :before
         *    element to mask the overlaping shadow from the :after element.
         */
        @include media-query(2) {
            display: block;
            flex: 1 0 0px;
            margin: 0 $hsu;

            &:first-of-type,
            &:last-of-type {

                &:after { /* [1} */
                    background: inherit;
                    border-radius: 0 0 12px 12px;
                    box-shadow: 0 0 36px 0 rgba($brandBlack, 0.1);
                    bottom: 0;
                    content: "";
                    display: block;
                    left: 0;
                    height: 40px;
                    position: absolute;
                    transform-origin: left bottom;
                    transform: skewY(3deg);
                    width: 100%;
                    z-index: 2;
                }
                &:before { /* [2] */
                    background: inherit;
                    bottom: 0;
                    border-radius: 12px;
                    display: block;
                    content: "";
                    height: 100%;
                    left: 0;
                    position: absolute;
                    width: 100%;
                    z-index: 3;
                }
            }

            &:first-of-type {
                border-radius: 12px 12px 12px 0;
                margin-left: 0;

                &:after { /* [1} */
                    transform-origin: right bottom;
                    transform: skewY(-3deg);
                }
            }

            &:last-of-type {
                border-radius: 12px 12px 0 12px;
                margin-right: 0;

                &:after { /* [1} */
                    transform-origin: left bottom;
                    transform: skewY(3deg);
                }
            }
        }

        /**
         * Media query adjustments
         */
        @include media-query(4) {
            padding: $hsu $bsu * 1.5 $bsu * 1.5;
            margin: 0 $bsu * 1.5;
        }
    }

    &__image {
        display: block;
        margin: 0 auto $hsu;
        position: relative;
        width: 60%;
        z-index: 4;

        /**
         * Media query adjustments.
         *
         * When the image is left aligned we need to reduce it's
         * size otherwise it outscales the text on resize.
         */
        @include media-query(1) {
            float: left;
            margin: 0 $bsu 0 0;
            width: 30%;
        }

        /**
         * Media query adjustments.
         *
         * We reset the image at breakpoint 2.
         */
        @include media-query(2) {
            float: none;
            margin: 0 auto $hsu;
            width: 60%;
        }
    }

    &__title {
        @extend .regular-serif, .extra-large-text;
    }

    &__header,
    &__body,
    &__footer {
        position: relative;
        z-index: 4;
    }

    /**
     * Media query adjustments.
     *
     * The section summary items appear stacked until breakpoint
     * 2 when we apply flex to align them and maintain equal heights.
     */
    @include media-query(2) {
        align-items: stretch;
        display: flex;
        justify-content: space-between;
    }
}