/**
 * Font weights.
 */
.regular {
    @include font-stack(regular, sans-serif, normal, true);
}
.regular-italic {
    @include font-stack(regular, sans-serif, italic, true);
}
.bold {
    @include font-stack(bold, sans-serif, normal, true);
}
.bold-italic {
    @include font-stack(bold, sans-serif, italic, true);
}
.regular-serif {
    @include font-stack(regular, serif, normal, true);
}
.regular-italic-serif {
    @include font-stack(regular, serif, italic, true);
}

/**
 * Text sizes.
 *
 * We scale up the type sizes a breakpoint 2.
 */
$small-text: 12;
$bp2-small-text: 14;

$default-text: 14;
$bp2-default-text: 16;

$medium-text: 16;
$bp2-medium-text: 18;

$large-text: 18;
$bp2-large-text: 23;

$extra-large-text: 21;
$bp2-extra-large-text: 28;

.small-text {
    @include type-scale($small-text, $font-base);

    /**
     * Media query adjustments.
     */
    @include media-query(2) {
        @include type-scale($bp2-small-text, $bp2-font-base);
    }
}
.default-text {
    @include type-scale($default-text, $font-base);

    /**
     * Media query adjustments.
     */
    @include media-query(2) {
        @include type-scale($bp2-default-text, $bp2-font-base);
    }
}
.medium-text {
    @include type-scale($medium-text, $font-base);

    /**
     * Media query adjustments.
     */
    @include media-query(2) {
        @include type-scale($bp2-medium-text, $bp2-font-base);
    }
}
.large-text {
    @include type-scale($large-text, $font-base);

    /**
     * Media query adjustments.
     */
    @include media-query(2) {
        @include type-scale($bp2-large-text, $bp2-font-base);
    }
}
.extra-large-text {
    @include type-scale($extra-large-text, $font-base);

    /**
     * Media query adjustments.
     */
    @include media-query(2) {
        @include type-scale($bp2-extra-large-text, $bp2-font-base);
    }
}

/**
 * Alternative anchors.
 */
%inverted-anchor {
    color: color(base, anchor-hover);
    text-decoration: none;

    &[href]:hover,
    &:hover {
        color: color(base, anchor);
    }
}
%underline-anchor {
    color: color(base, anchor-hover);
    text-decoration: none;

    &:hover {
        border-bottom: 1px solid color(base, anchor-hover);
        color: color(base, anchor-hover);
    }
}

/**
 * Soft Break.
 */
.soft-br {
    display: inline;
    
    /**
     * Media query adjustments
     */
    @include media-query(2) {
        display: block;
    }
}

/**
 * Lists.
 */
.flatten {
    list-style: none;
    padding: 0;
}


/**
 * Clearfix
 */
.cf:after {
    content: "";
    display: table;
    clear: both;
}