.flex-grid {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 0 -39px;
}
.flex-grid > * {
  -webkit-box-flex: 0.5;
      -ms-flex: 0.5 0 320px;
          flex: 0.5 0 320px;
  margin: 18px 39px;
  &:last-of-type {
    margin-bottom: 18px;
  }
}

.article-list {
  padding: 0;
}
.article-list li {
  list-style-type: none;
}