/**
 * Text inputs
 */
input[type="text"],
input[type="email"],
input[type="tel"],
input[type="password"] {
    @extend .small-text;

    background-color: color(input, bg);
    border: 1px solid color(input, border);
    border-radius: 12px;
    color: color(input, text);
    outline: none;
    display: block;
    padding: $hsu * .75 + 1px $hsu $hsu * .75;

    &::placeholder {
        color: color(input, placeholder);
        letter-spacing: 1px;
        text-transform: uppercase;
    }

    /**
     * Media query adjustments
     */
    @include media-query(2) {
        padding: $hsu + 2px $bsu;
    }
}