blockquote {
    @extend .large-text, .regular-italic;

    color: color(base, blockquote);
    margin: 0 0 $bsu;

    p:first-of-type {

        &:before {
            content: "\201C";
        }
    }

    p:last-of-type {
        margin-bottom: 0;

        &:after {
            content: "\201D";
        }
    }
}