$namespace: ".grid";

/**
 * Component styles.
 */
#{$namespace} {

    /**
     * Grid row.
     *
     * 1. Columns have top and bottom padding at smaller resolutions to supply
     *    separation when they're stacked. We're remove the top padding from the
     *    first column and bottom padding from the last column on each row to
     *    avoid excessive padding.
     */
    &__row {
        @extend .cf;

        padding: 0 $bsu * .75;

        #{$namespace} {

            &__column {

                &:first-of-type { /* [1] */
                    padding-top: 0;
                }
                &:last-of-type { /* [1] */
                    padding-bottom: 0;
                }
            }
        }
    }

    /**
     * Grid column.
     *
     * Note that both .grid__column and a modifier need to be applied to
     * an element.
     *
     * 1. Full: Full width column.
     * 2. One Third: One third width column.
     * 3. Two Thirds: Two third width column.
     * 4. Half: One half width column.
     */
    &__column {
        float: left;
        min-height: 1px;
        padding: $bsu 0;
        position: relative;
        width: 100%;

        &--full {

            /**
             * Media query adjustments.
             */
            @include media-query(3) {
                padding: 0 $hsu;
                width: 100%;
            }
            
            /**
             * Media query adjustments
             */
            @include media-query(4) {
                padding: 0 $bsu * 1.5;
            }
        }

        &--one-third {

            /**
             * Media query adjustments.
             */
            @include media-query(3) {
                padding: 0 $hsu;
                width: 33.333%;
            }
            
            /**
             * Media query adjustments
             */
            @include media-query(4) {
                padding: 0 $bsu * 1.5;
            }
        }

        &--two-thirds {

            /**
             * Media query adjustments.
             */
            @include media-query(3) {
                padding: 0 $hsu;
                width: 66.666%;
            }

            /**
             * Media query adjustments
             */
            @include media-query(4) {
                padding: 0 $bsu * 1.5;
            }
        }

        &--half {

            /**
             * Media query adjustments.
             */
            @include media-query(3) {
                padding: 0 $hsu;
                width: 50%;
            }
            
            /**
             * Media query adjustments
             */
            @include media-query(4) {
                padding: 0 $bsu * 1.5;
            }
        }

        &--one-sixth {

            /**
             * Media query adjustments.
             */
            @include media-query(3) {
                padding: 0 $hsu;
                width: 16.666%;
            }
            
            /**
             * Media query adjustments
             */
            @include media-query(4) {
                padding: 0 $bsu * 1.5;
            }
        }

        &--four-sixths {

            /**
             * Media query adjustments.
             */
            @include media-query(3) {
                padding: 0 $hsu;
                width: 66.666%;
            }
            
            /**
             * Media query adjustments
             */
            @include media-query(4) {
                padding: 0 $bsu * 1.5;
            }
        }

        &--five-sixths {

            /**
             * Media query adjustments.
             */
            @include media-query(3) {
                padding: 0 $hsu;
                width: 83.333%;
            }
            
            /**
             * Media query adjustments
             */
            @include media-query(4) {
                padding: 0 $bsu * 1.5;
            }
        }
    }
}