$namespace: ".section-heading";

#{$namespace} {
    @extend .small-text, .regular;

    color: color(section-heading, title);
    letter-spacing: 2px;
    text-transform: uppercase;
    white-space: nowrap;

    .icon {
        @include type-scale(18, $font-base);

        background-color: color(section-heading, icon-bg);
        border-radius: 6px;
        color: color(section-heading, icon);
        display: inline-block;
        height: 34px;
        line-height: 34px;
        margin-right: $qsu;
        padding: 0 $qsu;
        position: relative;
        text-align: center;
        top: 2px;
        width: 34px;
    }
}