html {

    /**
     * Media query adjustments
     */
    @include media-query(2) {
        font-size: percentage($bp2-font-size / 100);
    }
}

/**
 * Headings
 */
h1,h2,h3,h4,h5,h6 {
    @extend .bold;

    line-height: 1.2;
    margin-bottom: $hsu;
}
h4,h5,h6 {
    line-height: 1.6;
}

/**
 * Media query adjustments
 */
@include media-query(2) {

    $sizes: tera $bp2-tera, giga $bp2-giga, mega $bp2-mega, alpha $bp2-alpha, beta $bp2-beta, gamma $bp2-gamma, delta $bp2-delta, epsilon $bp2-epsilon, zeta $bp2-zeta;

    @each $size in $sizes {
        .#{nth($size, 1)} {
            @include type-scale(nth($size, 2), $bp2-font-base, '#{$type-scale-unit-value}', $measure);

            margin-bottom: $bsu;
        }
    }
}

/**
 * Paragraphs
 */
.lead {
    @extend .medium-text;
}

/**
 * Anchors
 */
a {
    text-decoration: none;
}

/**
 * Lists
 */
ul {
    list-style: square;
}

/**
 * Horizontal Rule
 */
hr {
    background-color: color(main, body-border);
    height: $bsu;
    border: none;
}