$namespace: ".podcast-banner";

/**
 * Component styles.
 */
#{$namespace} {
    @extend .cf;

    background-color: color(podcast-banner, bg);
    border-radius: 12px;
    position: relative;

    &__link {
        display: block;
        text-decoration: none;
    }
    &__image {
        background-position: left center;
        background-size: cover;
        border-radius: 12px 0 0 12px;
        bottom: 0;
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 45%;
    }
    &__body {
        margin-left: 45%;
        width: 55%;
    }
    &__title {
        @extend .large-text;

        color: color(podcast-banner, title-hover);
        line-height: 1.4;
        margin-bottom: 0;
        padding: $bsu $bsu * .75;

        small {
            @extend .small-text;
            @include font-stack(light, sans-serif);

            display: block;
            padding-top: $hsu;
        }
    }

    &:hover {
        box-shadow: global(red-outer-glow);
    }
}