$namespace: ".content-listing";

#{$namespace} {


    /**
     * Media query adjustments
     */
    @include media-query(3) {

        .grid__row {

            .resource {
                margin-bottom: $bsu * 2.5;
            }

            &:last-of-type {

                .resource {
                    margin-bottom: 0;
                }
            }
        }
    }
}