/*------------------------------------*\
    $GLOBALS
\*------------------------------------*/
$globals: (

    // Border Radius
    border-radius: 12px,
    half-border-radius: 6px,

    // Shadows
    black-outer-glow: 0 0 36px 0 rgba($brandBlack, 0.2),
    black-outer-glow-hover: 0 0 36px 0 rgba($brandBlack, 0.4),
    black-bottom-shadow: 10px 0 36px 0 rgba($brandBlack, 0.2),
    red-outer-glow: 0 0 36px 1px rgba($brandRed, 0.2),
    red-outer-glow-hover: 0 0 36px 1px rgba($brandRed, 0.4)
)