$namespace: ".search-form";

#{$namespace} {
    @extend .cf;

    &__query {
        float: left;
        margin-right: $hsu;
        width: 180px;

        /**
         * Media query adjustments.
         *
         * We restrict the width of the email input at breakpoint 3.
         */
        @include media-query(3) {
            max-width: 600px;
            width: 100%;
        }
    }
    &__submit {
        float: left;
    }
}