$namespace: ".nav";

/**
 * Component styles.
 *
 * 1. Primary: The primary overlaying navigation for the site.
 * 2. Secondary: The secondary navigation is a simple listing of links.
 * 3. Social: The social navigation comprises of social network icon links.
 * 4. Breadcrumb: A breadcrumb navigation used on the content pages.
 * 5. Sidebar Menu: The sidebar menu is used for hierarchical content.
 */
#{$namespace} {

    &--primary { /* [1] */

        #{$namespace} {

            &__bar {
                background-color: #FFF;
                box-shadow: global(black-bottom-shadow);
                display: none;
                left: 0;
                padding: 0 $hsu;
                position: fixed;
                top: 0;
                right: 0;
                width: 100%;


                /**
                 * Media query adjustments
                 */
                @include media-query(3) {
                    padding: 0 $bsu * 2.5;
                }
            }

            &__logo {
                background-size: 100% auto;
                height: 60px;
                left: $hsu;
                margin: 0;
                padding: 0;
                position: relative;
                width: 60px;
            }

            &__container {
                background-color: color(nav, primary-bg);
                border: $hsu * .75 solid color(nav, primary-border);
                bottom: $bsu * .75;
                box-shadow: 0 0 $hsu * .5 50px color(nav, primary-outer-border);
                display: none;
                padding: $bsu * 2.5 $bsu $bsu;
                position: fixed;
                top: $hsu * .75;
                left: $hsu * .75;
                right: $hsu * .75;
                z-index: 10;

                /**
                 * Media query adjustments
                 */
                @include media-query(3) {
                    border: $bsu * .5 solid color(nav, primary-border);
                    box-shadow: 0 0 $bsu * .75 50px color(nav, primary-outer-border);
                    padding: $bsu * 1.5;
                    top: $bsu * .75;
                    left: $bsu * .75;
                    right: $bsu * .75;
                }
            }

            &__items {

                /**
                 * Media query adjustments
                 */
                @include media-query(3) {
                    align-content: stretch;
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: space-between;
                }
            }
            &__item {
                border-bottom: 1px solid color(nav, primary-divider);
                display: block;
                margin-bottom: $hsu;
                padding: $hsu * .25 0;

                &:last-of-type {
                    border: none;
                }

                /**
                 * Media query adjustments
                 */
                @include media-query(3) {
                    border: none;
                    display: inline-block;
                    margin-bottom: $bsu * 1.5;
                    padding: 0;
                    width: 28%;
                }
            }
            &__title {
                @extend .regular-serif, .extra-large-text;

                color: color(nav, primary-title);
                margin-bottom: 0;
                transition: color .25s ease;
            }
            &__desc {
                color: color(nav, primary-desc);
                display: none;
                opacity: .5;
                transition: opacity .25s ease;

                /**
                 * Media query adjustments
                 */
                @include media-query(3) {
                    display: block;
                }
            }

            &__link {
                display: block;

                &:hover {

                    #{$namespace} {

                        &__title {
                            color: color(nav, primary-title-hover);
                        }

                        &__desc {
                            opacity: 1;
                        }
                    }
                }
            }

            &__handle {
                color: color(nav, handle);
                cursor: pointer;
                height: 23px;
                overflow: hidden;
                padding-right: $bsu * 1.75;
                position: absolute;
                right: $bsu * 1.5;
                transform: rotate(0deg);
                top: $bsu * 1.5;
                z-index: 20;

                span {
                    background-color: color(nav, handle);
                    display: block;
                    height: 3px;
                    opacity: 1;
                    position: absolute;
                    right: $bsu;
                    transform: rotate(0deg);
                    transition: .25s ease-in-out;
                    width: 28px;

                    &:nth-child(1) {
                        top: 0px;
                    }

                    &:nth-child(2) {
                        top: 10px;
                    }

                    &:nth-child(3) {
                        top: 20px;
                    }
                }

                /**
                 * Media query adjustments
                 */
                @include media-query(3) {
                    right: $bsu * 3;
                    top: $bsu * 2.5;
                }
            }

            &__handle-label {
                text-transform: uppercase;
            }

            &__handle-container {
                position: absolute;
                height: 23px;
                top: 0;
                right: -$bsu;
                width: 31px + $bsu;
            }
        }

        &#{$namespace}--inverted {

            #{$namespace} {

                &__handle-label {
                    color: color(nav, handle-inverted);
                }
                &__handle-container span {
                    background-color: color(nav, handle-inverted);
                }
            }

            &.open,
            &.fixed {

                #{$namespace} {

                    &__handle-label {
                        color: color(nav, handle);
                    }
                    &__handle-container span {
                        background-color: color(nav, handle);
                    }
                }
            }
        }


        &.open {

            #{$namespace} {

                &__container {
                    display: block;
                }

                &__handle {
                    color: color(nav, handle);
                    position: fixed;

                    span {
                        background-color: color(nav, handle);

                        &:nth-child(1) {
                            top: 11px;
                            transform: rotate(-45deg);
                        }

                        &:nth-child(2) {
                            opacity: 0;
                            right: -60px;
                        }

                        &:nth-child(3) {
                            top: 11px;
                            transform: rotate(45deg);
                        }
                    }
                }
            }
        }

        &.fixed {
            position: fixed;
            top: 0;
            width: 100%;
            z-index: 10;

            &.open {

                #{$namespace} {

                    &__handle {
                        top: $bsu * 1.5;
                        
                        /**
                         * Media query adjustments
                         */
                        @include media-query(3) {
                            top: $bsu * 2.5;
                        }
                    }
                }
            }

            #{$namespace} {

                &__bar {
                    display: block;
                }

                &__handle {
                    top: $bsu * .70;
                    right: $bsu * 1.5;

                    /**
                     * Media query adjustments
                     */
                    @include media-query(3) {
                        right: $bsu * 3;
                    }
                }
            }
        }
    }

    &--secondary { /* [2] */

        #{$namespace} {

            &__item {
                display: block;

                &:first-of-type {
                    margin-left: 0;
                }
                &:last-of-type {
                    margin-right: 0;
                }
                
                /**
                 * Media query adjustments.
                 *
                 * Navigation items align in a row at breakpoint 3.
                 */
                @include media-query(3) {
                    display: inline-block;
                    margin: 0 $bsu;
                }
            }

            &__link {
                @extend .bold;

                color: color(nav, secondary-link);
                text-decoration: none;

                &:hover {
                    color: color(nav, secondary-link-hover);
                }

                .icon {
                    position: relative;
                    top: 2px;

                    &--exit-arrow {
                        margin-left: $hsu * .5;
                    }
                }
            }
        }
    }

    &--social { /* [3] */

        #{$namespace} {

            &__item {
                margin: 0 $hsu * .25;

                &:first-of-type {
                    margin-left: 0;
                }
                &:last-of-type {
                    margin-right: 0;
                }

                /**
                 * Media query adjustments.
                 *
                 * We increase the spacing between the navigation items at breakpoint 3.
                 */
                @include media-query(3) {
                    margin: 0 $hsu * .5;
                }
            }

            &__link {
                @extend .default-text;

                border: 2px solid color(nav, social-link);
                border-radius: 9px;
                color: color(nav, social-link);
                line-height: 24px;
                padding-top: 2px;
                text-align: center;
                text-decoration: none;
                width: 30px;

                &:hover {
                    border: 2px solid color(nav, social-link-hover);
                    color: color(nav, social-link-hover);
                }

                /**
                 * Media query adjustments.
                 *
                 * We scale up the size of the navigation items at breakpoint 3.
                 */
                @include media-query(3) {
                    @include type-scale(18, $font-base);

                    border-radius: 12px;
                    line-height: 30px;
                    width: 35px;
                }
            }
        }
    }

    &--breadcrumb { /* [4] */

        #{$namespace} {

            &__item {
                color: color(nav, breadcrumb-active);
                padding-right: $bsu;
                position: relative;

                &:after {
                    display: block;
                    content: "\003e";
                    position: absolute;
                    top: 0;
                    right: $hsu - 6px;
                }

                &:last-of-type {

                    &:after {
                        display: none;
                    }
                }
            }

            &__link {
                color: color(nav, breadcrumb-text);
            }
        }
    }

    &--sidebar-menu {

        #{$namespace} {

            &__title {
                @extend .large-text, .regular;

                margin-bottom: $hsu;
                padding: 0 $bsu * 1.5 0 $bsu * 2 + 5px;
            }

            &__item {
                background-color: color(nav, sidebar-parent-bg);
                border-bottom: 1px solid color(nav, sidebar-divider);
                display: block;
                position: relative;

                &:first-of-type {
                    border-top: 1px solid color(nav, sidebar-divider);
                }

                &.open {

                    #{$namespace} {

                        &__link {
                            border-color: color(nav, sidebar-parent-border);
                        }

                        &__child-items {
                            display: block;
                        }
                    }

                    .icon {
                        transform: rotate(180deg);
                    }
                }

                .icon {
                    display: block;
                    padding: $hsu;
                    position: absolute;
                    right: $bsu * 1.5 - $hsu;
                    top: 5px;
                }
            }

            &__link {
                @include font-stack(bold, sans-serif);
            }

            &__child-items {
                display: none;
            }

            &__child-item {
                background-color: color(nav, sidebar-child-bg);
                border-top: 1px solid color(nav, sidebar-divider);
                display: block;
            }

            &__link,
            &__child-link {
                border-left: 5px solid transparent;
                color: color(nav, sidebar-text);
                display: block;

                &:hover {
                    color: color(nav, sidebar-text-hover);

                    .icon {
                        color: color(nav, sidebar-text);
                    }
                }

                &.active {
                    color: color(nav, sidebar-text-hover);
                }
            }
            &__link {
                padding: $hsu $hsu $hsu $bsu * 2;
                width: 75%;
            }
            &__child-link {
                padding: $hsu $bsu * 2;
            }
        }
    }
}