/*------------------------------------*\
    $PROJECT
\*------------------------------------*/
/**
 * Aliases
 */
$bsu: $base-spacing-unit;
$hsu: $half-spacing-unit;
$qsu: $hsu / 2;


/**
 * Components
 * ----------------------------------------//
 */
$use-typography:  true;
$use-grid:        true;
$use-island:      true;
$use-flag:        true;
$use-nav:         true;

/**
 * Typography
 * ----------------------------------------//
 */
$line-height:      1.65;
$font-size:        87.5; // 14px
$font-base:        16 * ($font-size / 100);

$bp2-font-size:    100; // 16px
$bp2-font-base:    16 * ($bp2-font-size / 100);

$sans-serif-stack:  "adelle-sans", sans-serif;
$serif-stack:  "eloquent-jf-pro", serif;

$sans-serif-weights: ( regular:   400,
                       bold:      700 );
$serif-weights: ( regular:   400 );

$stack-map:   (sans-serif: $sans-serif-stack, serif: $serif-stack);
$weights-map: (sans-serif: $sans-serif-weights, serif: $serif-weights);



/**
 * Typescale
 * ------------------------------//
 */
$tera:    90;
$giga:    50;
$mega:    50;
$alpha:   38;
$beta:    30;
$gamma:   24;
$delta:   21;
$epsilon: 16;
$zeta:    14;

$bp2-tera:    104;
$bp2-giga:    88;
$bp2-mega:    64;
$bp2-alpha:   53;
$bp2-beta:    42;
$bp2-gamma:   32;
$bp2-delta:   21;
$bp2-epsilon: 18;
$bp2-zeta:    16;


/**
 * Grid
 * ----------------------------------------//
 */
$grid-breakpoints-list:  1, 2, 3, 4;
$grid-breakpoint-widths: 30em, 48em, 60em, 90em;